import { useTranslation } from "react-i18next";

import { HvButton } from "@hitachivantara/uikit-react-core";

import { HvAppShellHelp } from "@hitachivantara/app-shell-shared";

import { CONFIG_TRANSLATIONS_NAMESPACE } from "../../../../../../lib/i18n";

import includesString from "../../../../../../lib/utils/textUtil";
import IconUiKit from "../../../../../IconUiKit";

const HelpButton = ({ url, description }: HvAppShellHelp) => {
  const { t } = useTranslation();
  const { t: tConfig } = useTranslation(CONFIG_TRANSLATIONS_NAMESPACE);

  if (!url) {
    return null;
  }

  const finalUrl = includesString(url, ":") ? url : tConfig(url);

  const finalDescription = description
    ? tConfig(description)
    : t("header.helpUrl.documentationLink");

  return (
    <HvButton
      icon
      variant="secondaryGhost"
      component="a"
      href={finalUrl}
      target="_blank"
      aria-label={finalDescription}
      rel="noopener,noreferrer">
      <IconUiKit name="Help" />
    </HvButton>
  );
};

export default HelpButton;
